import React from 'react';
import Buttons from './Buttons';	

const Footer = (props)=>{
  let invertStyle = {backgroundColor:"white",color:"black",padding:"0.25em"}
  return(
    <div class='footer f'>
    	<Buttons style={invertStyle}/>
      	<p>Jon R. © {new Date().getFullYear()}</p>
    </div>
  );
}

export default Footer;