import React,{useState,useEffect} from 'react';
import About from './About';
import Education from './Education';
import Certificates from './Certificates';
import Experience from './Experience';
import Projects from './Projects';
import Skills from './Skills';
import Links from './Links';

const Body = (props)=>{
  return (
    <div style={{paddingTop:'180px',paddingBottom:'180px'}}>
	{(props.page=='about')&&<About/>}
    {(props.page=='education')&&<Education/>}	
    {(props.page=='certificates')&&<Certificates/>}	
    {(props.page=='experience')&&<Experience/>}	
    {(props.page=='projects')&&<Projects/>}	
    {(props.page=='skills')&&<Skills/>}	
    {(props.page=='links')&&<Links/>}	
    </div>
  );
}

export default Body;