import React,{useState,useEffect} from 'react';

const Box = (props)=>{
  const [lang,setLang] = useState(0);
  const [image,setImage] = useState('');
  const [title,setTitle] = useState(['']);
  const [text,setText] = useState([]);
  const [etext,setEtext] = useState([]);
  const [jtext,setJtext] = useState([]);
  const [ctext,setCtext] = useState([]);
  const [view,setView] = useState(true);
  function viewDetails(){
    (view)?setView(false):setView(true);
  }
  useEffect(()=>{
    const url ='https://spreadsheets.google.com/feeds/list/1EYJ9WPjqKqe26InIhdzyFKZzBKgAKRkM04w7aaFQhxk/od6/public/values?alt=json';
    fetch(url) 
    .then(res => res.json())
    .then(data=>data.feed.entry)
    .then(entry=>{
      let eitems = [];
      let jitems = [];
      let citems = [];
      let i = 0;
      entry.forEach(e=>{
        let index = 'gsx$'+props.class;
        let item = e[index]['$t'];
        if(item.length>0){
          switch(i%3){
            case 1: eitems.push(item); break;
            case 2: jitems.push(item); break;
            case 0: citems.push(item); break;
            default: break;
          }
          i++;
        }
      })
      if(props.handleLang) props.handleLang(0);
      //console.log("lang"+lang);
      let items = (props.handleLang==0)?eitems:(props.handleLang==1)?jitems:citems;
      setTitle(items[0]);
      console.log("title"+title);
      /*console.log("eng"+eitems);
      console.log("jpn"+jitems);
      console.log("chi"+citems);*/
      setImage(items[1]);
      //console.log(items);
      eitems.shift();
      eitems.shift();
      console.log(eitems);
      setText(eitems);
      jitems.shift();
      jitems.shift();
      console.log(jitems);
      setJtext(jitems);
      citems.shift();
      citems.shift();
      console.log(citems);
      setCtext(citems);
      (props.handleLang==0)?setText(eitems):(props.handleLang==1)?setText(jitems):setText(citems);
      /*setJtext(jitems);
      setCtext(citems);
      console.log(text);
      console.log(jtext);
      console.log(ctext);*/
    });
  },[]);
  // lan{lang}<br/>japanese{jtext.map(t=>{return <>{t}<br/></>})}<br/>chinese{ctext.map(t=>{return <>{t}<br/></>})}<br/>english
  //{text.map(t=>{return <>{t}<br/></>})}
  //{props.class!='about'&&<button style={btnStyle}><b>View</b></button>}
  let imgStyle = {}
  if(props.class=='about'){
    imgStyle = {minHeight:'300px',border:'0.5em black solid',borderRadius:'100em',margin:'1em'}
  } else{
    imgStyle = {height:'100px',padding:'1em'}
  }
  return(
    <div class='box f' id={props.class}>
    {(image!='')&&<img src={image} style={imgStyle}></img>}
    <h3 onClick={viewDetails}>{title}</h3>
    {(view)&&<p class='text'>
    <p>{
      text.map(t=>{return <>{t}<br/></>})
    }</p>
    </p>}
    </div>
  );
}

export default Box;