
import React from 'react';
import Header from './Header';
import Body from './Body';
import Footer from './Footer';

class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      splash: true,
      count: 0,
      page: 'about',
      lang: 0
    };
  }
  setPage = (p)=>{
    this.setState({page:p});
    console.log(this.state.page);
  }
  getLang = ()=>{
    return this.state.lang;
  }
  setLang = (l)=>{
    this.setState({lang:l},()=>{
      console.log(this.state.lang);
    });
  }
  renderSplash(){
    /*let splashStyle = {
            height: "100vh", 
            width: "100vw", 
            display: "flex", 
            margin:'0',
            fontSize: '2em',
            justifyContent: "center", 
            alignItems: "center",
            borderRadius: '0'
            {(this.state.splash)?this.renderSplash():<Body time={this.state.count}/>}
        }*/
    setInterval(()=>{
      this.setState({count:this.state.count+1})
      },100);
    setTimeout(()=>{
      this.setState({splash:false})
      },1000);
    return(
      <Body time={this.state.count}/>
    );
  }
  render(){
    return(
      <>
      <Header handlePage={this.setPage} lang={this.state.lang} handleLang={this.setLang}/>
      <Body page={this.state.page} handleLang={this.setLang} style={{padding:'700px'}}/>
      <Footer name={this.props.name}/>
      </>
    );
  }
}

export default App;